import Image from "next/image";
import React, { useState } from 'react';
import { 
  User, 
  Briefcase, 
  GraduationCap, 
  Code, 
  Linkedin, 
  Mail, 
  FileText, 
  Award,
  ExternalLink,
  Github,
  Phone
} from 'lucide-react';

const PortfolioBinder = () => {
  const [isOpen, setIsOpen] = useState(false);
  const [selectedCard, setSelectedCard] = useState<PortfolioSection | null>(null);

  const portfolioSections = [
    {
      id: 'about',
      title: 'About Me',
      icon: User,
      color: 'from-blue-600 to-purple-600',
      content: {
        title: 'John Doe',
        subtitle: 'Full Stack Developer',
        description: 'Passionate developer with 5+ years of experience building scalable web applications. I love turning complex problems into simple, beautiful solutions.',
        details: [
          'Location: San Francisco, CA',
          'Experience: 5+ years',
          'Specialization: React, Node.js, TypeScript'
        ]
      }
    },
    {
      id: 'projects',
      title: 'Projects',
      icon: Code,
      color: 'from-green-600 to-teal-600',
      content: {
        title: 'Featured Projects',
        items: [
          {
            name: 'E-Commerce Platform',
            tech: 'React, Node.js, PostgreSQL',
            description: 'Built a full-stack e-commerce solution with payment integration'
          },
          {
            name: 'Task Management App',
            tech: 'Next.js, TypeScript, Prisma',
            description: 'Collaborative project management tool with real-time updates'
          },
          {
            name: 'Data Visualization Dashboard',
            tech: 'React, D3.js, Python',
            description: 'Interactive dashboard for business analytics and reporting'
          }
        ]
      }
    },
    {
      id: 'experience',
      title: 'Work Experience',
      icon: Briefcase,
      color: 'from-orange-600 to-red-600',
      content: {
        title: 'Professional Experience',
        items: [
          {
            role: 'Senior Frontend Developer',
            company: 'TechCorp Inc.',
            period: '2022 - Present',
            description: 'Led development of customer-facing applications, improved performance by 40%'
          },
          {
            role: 'Full Stack Developer',
            company: 'StartupXYZ',
            period: '2020 - 2022',
            description: 'Built MVP from scratch, scaled to 10k+ users'
          },
          {
            role: 'Junior Developer',
            company: 'DevAgency',
            period: '2019 - 2020',
            description: 'Developed responsive websites and learned modern frameworks'
          }
        ]
      }
    },
    {
      id: 'education',
      title: 'Education',
      icon: GraduationCap,
      color: 'from-purple-600 to-pink-600',
      content: {
        title: 'Academic Background',
        items: [
          {
            degree: 'Bachelor of Science in Computer Science',
            school: 'Stanford University',
            period: '2015 - 2019',
            gpa: 'GPA: 3.8/4.0'
          },
          {
            degree: 'Full Stack Web Development Bootcamp',
            school: 'General Assembly',
            period: '2019',
            description: 'Intensive 12-week program'
          }
        ]
      }
    },
    {
      id: 'skills',
      title: 'Skills & Certifications',
      icon: Award,
      color: 'from-indigo-600 to-blue-600',
      content: {
        title: 'Technical Skills',
        categories: [
          {
            name: 'Frontend',
            skills: ['React', 'TypeScript', 'Next.js', 'Tailwind CSS', 'Vue.js']
          },
          {
            name: 'Backend',
            skills: ['Node.js', 'Python', 'PostgreSQL', 'MongoDB', 'GraphQL']
          },
          {
            name: 'Tools',
            skills: ['Git', 'Docker', 'AWS', 'Figma', 'Jest']
          }
        ]
      }
    },
    {
      id: 'linkedin',
      title: 'LinkedIn',
      icon: Linkedin,
      color: 'from-blue-700 to-blue-500',
      content: {
        title: 'Professional Network',
        description: 'Connect with me on LinkedIn to see my full professional history and network.',
        link: 'https://linkedin.com/in/ewong05',
        stats: [
          '500+ Connections',
          '50+ Recommendations',
          'Open to opportunities'
        ]
      }
    },
    {
      id: 'resume',
      title: 'Resume',
      icon: FileText,
      color: 'from-gray-700 to-gray-500',
      content: {
        title: 'Download Resume',
        description: 'Get a comprehensive overview of my experience, skills, and achievements.',
        formats: [
          { type: 'PDF', size: '245 KB' },
          { type: 'Word', size: '180 KB' }
        ]
      }
    },
    {
      id: 'contact',
      title: 'Contact',
      icon: Mail,
      color: 'from-teal-600 to-green-600',
      content: {
        title: 'Get In Touch',
        description: 'Ready to discuss opportunities or collaborate on projects.',
        methods: [
          { type: 'Email', value: 'john.doe@email.com', icon: Mail },
          { type: 'Phone', value: '+1 (555) 123-4567', icon: Phone },
          { type: 'GitHub', value: 'github.com/johndoe', icon: Github }
        ]
      }
    }
  ];

  const handleBinderClick = () => {
    if (!isOpen) {
      setIsOpen(true);
    }
  };

  interface AboutContent {
    title: string;
    subtitle: string;
    description: string;
    details: string[];
  }

  interface ProjectItem {
    name: string;
    tech: string;
    description: string;
  }

  interface ExperienceItem {
    role: string;
    company: string;
    period: string;
    description: string;
  }

  interface EducationItem {
    degree: string;
    school: string;
    period: string;
    gpa?: string;
    description?: string;
  }

  interface SkillsCategory {
    name: string;
    skills: string[];
  }

  interface SkillsContent {
    title: string;
    categories: SkillsCategory[];
  }

  interface LinkedinContent {
    title: string;
    description: string;
    link: string;
    stats: string[];
  }

  interface ResumeFormat {
    type: string;
    size: string;
  }

  interface ResumeContent {
    title: string;
    description: string;
    formats: ResumeFormat[];
  }

  interface ContactMethod {
    type: string;
    value: string;
    icon: React.ComponentType<{ className?: string }>;
  }

  interface ContactContent {
    title: string;
    description: string;
    methods: ContactMethod[];
  }

  type SectionContent =
    | AboutContent
    | { title: string; items: ProjectItem[] }
    | { title: string; items: ExperienceItem[] }
    | { title: string; items: EducationItem[] }
    | SkillsContent
    | LinkedinContent
    | ResumeContent
    | ContactContent;

  interface PortfolioSection {
    id: string;
    title: string;
    icon: React.ComponentType<{ className?: string }>;
    color: string;
    content: SectionContent;
  }

  const handleCardClick = (section: PortfolioSection) => {
    setSelectedCard(section);
  };

  const handleBackToCards = () => {
    setSelectedCard(null);
  };

  const handleCloseBinder = () => {
    setIsOpen(false);
    setSelectedCard(null);
  };

  const renderCardContent = (section: PortfolioSection) => {
    const content = section.content;
    
    switch (section.id) {
      case 'about':
        return (
          <div className="space-y-6">
            <div className="text-center">
              <div className="w-24 h-24 bg-gradient-to-br from-blue-600 to-purple-600 rounded-full mx-auto mb-4 flex items-center justify-center">
                <User className="w-12 h-12 text-white" />
              </div>
              <h3 className="section-title">{(content as AboutContent).title}</h3>
              <p className="text-brand text-blue-400">{(content as AboutContent).subtitle}</p>
            </div>
            <p className="text-primary">{(content as AboutContent).description}</p>
            <div className="space-y-2">
              {(content as AboutContent).details.map((detail, idx) => (
                <p key={idx} className="text-caption">{detail}</p>
              ))}
            </div>
          </div>
        );
      
      case 'projects':
      case 'experience':
      case 'education':
        return (
          <div className="space-y-6">
            <h3 className="section-title">{(content as { title: string; items: any[] }).title}</h3>
            <div className="space-y-4">
              {(content as { title: string; items: any[] }).items.map((item, idx) => (
                <div key={idx} className="p-4 rounded-lg" style={{ backgroundColor: 'var(--bg-card)' }}>
                  <h4 className="text-headline">
                    {item.name || item.role || item.degree}
                  </h4>
                  {item.company && <p className="text-nav">{item.company}</p>}
                  {item.school && <p className="text-nav">{item.school}</p>}
                  {item.tech && <p className="text-caption text-blue-400">{item.tech}</p>}
                  {item.period && <p className="text-caption">{item.period}</p>}
                  {item.gpa && <p className="text-caption">{item.gpa}</p>}
                  <p className="text-primary mt-2">{item.description}</p>
                </div>
              ))}
            </div>
          </div>
        );
      
      case 'skills':
        return (
          <div className="space-y-6">
            <h3 className="section-title">{(content as SkillsContent).title}</h3>
            <div className="space-y-4">
              {(content as SkillsContent).categories.map((category, idx) => (
                <div key={idx} className="p-4 rounded-lg" style={{ backgroundColor: 'var(--bg-card)' }}>
                  <h4 className="text-headline mb-3">{category.name}</h4>
                  <div className="flex flex-wrap gap-2">
                    {category.skills.map((skill, skillIdx) => (
                      <span 
                        key={skillIdx}
                        className="px-3 py-1 bg-blue-600/20 text-blue-300 rounded-full text-tag"
                      >
                        {skill}
                      </span>
                    ))}
                  </div>
                </div>
              ))}
            </div>
          </div>
        );
      
      case 'linkedin':
        return (
          <div className="space-y-6">
            <div className="text-center">
              <div className="w-16 h-16 bg-blue-600 rounded-lg mx-auto mb-4 flex items-center justify-center">
                <Linkedin className="w-8 h-8 text-white" />
              </div>
              <h3 className="section-title">{(content as LinkedinContent).title}</h3>
            </div>
            <p className="text-primary">{(content as LinkedinContent).description}</p>
            <div className="space-y-2">
              {(content as LinkedinContent).stats.map((stat, idx) => (
                <p key={idx} className="text-caption">{stat}</p>
              ))}
            </div>
            <button className="w-full bg-blue-600 hover:bg-blue-700 text-white py-3 rounded-lg text-button-lg flex items-center justify-center gap-2 transition-colors">
              Visit LinkedIn Profile
              <ExternalLink className="w-4 h-4" />
            </button>
          </div>
        );
      
      case 'resume':
        return (
          <div className="space-y-6">
            <div className="text-center">
              <div className="w-16 h-16 bg-gray-600 rounded-lg mx-auto mb-4 flex items-center justify-center">
                <FileText className="w-8 h-8 text-white" />
              </div>
              <h3 className="section-title">{(content as ResumeContent).title}</h3>
            </div>
            <p className="text-primary">{(content as ResumeContent).description}</p>
            <div className="space-y-3">
              {(content as ResumeContent).formats.map((format, idx) => (
                <button 
                  key={idx}
                  className="w-full p-3 rounded-lg border border-gray-600 hover:bg-gray-600/20 transition-colors flex items-center justify-between"
                >
                  <span className="text-button-lg">Download {format.type}</span>
                  <span className="text-caption">{format.size}</span>
                </button>
              ))}
            </div>
          </div>
        );
      
      case 'contact':
        return (
          <div className="space-y-6">
            <h3 className="section-title">{(content as ContactContent).title}</h3>
            <p className="text-primary">{(content as ContactContent).description}</p>
            <div className="space-y-4">
              {(content as ContactContent).methods.map((method, idx) => (
                <div key={idx} className="flex items-center gap-4 p-3 rounded-lg" style={{ backgroundColor: 'var(--bg-card)' }}>
                  <method.icon className="w-5 h-5 text-teal-400" />
                  <div>
                    <p className="text-caption">{method.type}</p>
                    <p className="text-primary">{method.value}</p>
                  </div>
                </div>
              ))}
            </div>
          </div>
        );
      
      default:
        return <div className="text-primary">Content coming soon...</div>;
    }
  };

  return (
    <div className="min-h-screen bg-gradient-to-br from-slate-900 via-slate-800 to-slate-900 flex items-center justify-center p-4">
      <div className="relative">
        {/* Closed Binder */}
        {!isOpen && (
          <div 
            onClick={handleBinderClick}
            className="w-96 h-72 cursor-pointer transform hover:scale-105 transition-all duration-500 hover:shadow-2xl"
          >
            <div className="relative w-full h-full">
              {/* Binder Cover */}
              <div className="absolute inset-0 bg-gradient-to-br from-amber-800 via-amber-700 to-amber-900 rounded-lg shadow-2xl">
                <div className="absolute inset-4 border-2 border-amber-600/30 rounded-md"></div>
                
                {/* Binder Rings */}
                <div className="absolute left-8 top-1/2 transform -translate-y-1/2 flex flex-col gap-4">
                  {[...Array(3)].map((_, i) => (
                    <div key={i} className="w-4 h-6 bg-gradient-to-r from-silver-400 to-silver-600 rounded-full shadow-inner border border-silver-300"></div>
                  ))}
                </div>
                
                {/* Cover Text */}
                <div className="absolute inset-0 flex flex-col items-center justify-center text-amber-100">
                  <h1 className="page-title text-center mb-2">Portfolio</h1>
                  <p className="text-brand">Click to Open</p>
                </div>
                
                {/* Leather texture effect */}
                <div className="absolute inset-0 rounded-lg opacity-20 bg-gradient-to-br from-transparent via-amber-900/50 to-amber-950"></div>
              </div>
            </div>
          </div>
        )}

        {/* Open Binder */}
        {isOpen && (
          <div className="w-full max-w-6xl">
            {selectedCard ? (
              /* Individual Card View */
              <div className="bg-slate-800/95 backdrop-blur-sm rounded-xl p-8 shadow-2xl border border-slate-700 max-w-2xl mx-auto">
                <div className="flex items-center justify-between mb-6">
                  <button
                    onClick={handleBackToCards}
                    className="text-blue-400 hover:text-blue-300 flex items-center gap-2 text-button-lg transition-colors"
                  >
                    ← Back to Portfolio
                  </button>
                  <button
                    onClick={handleCloseBinder}
                    className="text-slate-400 hover:text-slate-300 text-button-lg transition-colors"
                  >
                    Close
                  </button>
                </div>
                <div className="max-h-96 overflow-y-auto">
                  {renderCardContent(selectedCard)}
                </div>
              </div>
            ) : (
              /* Cards Grid View */
              <div className="bg-slate-800/95 backdrop-blur-sm rounded-xl p-8 shadow-2xl border border-slate-700">
                <div className="flex items-center justify-between mb-8">
                  <h1 className="page-title">Professional Portfolio</h1>
                  <button
                    onClick={handleCloseBinder}
                    className="text-slate-400 hover:text-slate-300 text-button-lg transition-colors"
                  >
                    Close Binder
                  </button>
                </div>
                
                <div className="grid grid-cols-2 md:grid-cols-4 gap-6">
                  {portfolioSections.map((section) => {
                    const IconComponent = section.icon;
                    return (
                      <div
                        key={section.id}
                        onClick={() => handleCardClick(section)}
                        className="group cursor-pointer transform hover:scale-105 transition-all duration-300"
                      >
                        <div className="bg-slate-700/50 hover:bg-slate-700 border border-slate-600 hover:border-slate-500 rounded-xl p-6 text-center transition-all duration-300 shadow-lg hover:shadow-xl">
                          <div className={`w-12 h-12 mx-auto mb-4 rounded-lg bg-gradient-to-br ${section.color} flex items-center justify-center group-hover:scale-110 transition-transform duration-300`}>
                            <IconComponent className="w-6 h-6 text-white" />
                          </div>
                          <h3 className="text-headline group-hover:text-white transition-colors">
                            {section.title}
                          </h3>
                        </div>
                      </div>
                    );
                  })}
                </div>
              </div>
            )}
          </div>
        )}
      </div>
    </div>
  );
}

export default PortfolioBinder;

